@media (min-width: 1920px) {
  .center {
    padding-left: calc((100% - 1440px) / 2);
    padding-right: calc((100% - 1440px) / 2);
  }

  .header {
    padding: 10px calc((100% - 1440px) / 2);
  }

  .nav-search {
    width: 100%;
  }

  .slider-caption {
    padding: 0 120px 0 60px;
  }

  .slider-caption h3 {
    max-width: 75%;
    margin-bottom: 100px;
  }

  .product-categories {
    max-width: 1440px;
  }

  .about-company {
    max-width: 1640px;
    margin: 80px auto 200px;
  }

  .about-text {
    padding: 130px 60px 130px 100px;
  }

  .footer {
    padding: 80px calc((100% - 1440px) / 2) 120px;
  }

  /* catalog */

  .slider-product-categories {
    padding-left: calc((100% - 1440px) / 2);
  }

  /* single */
  .single-wrapper {
    padding-left: calc((100% - 1440px) / 2);
    padding-right: calc((100% - 1440px) / 2);
  }

  .relative-products,
  .single-header {
    padding-left: calc((100% - 1440px) / 2);
    padding-right: calc((100% - 1440px) / 2);
  }
  
  
  /* service-page */
  
  .services-wrapper {
    padding-bottom: 100px;
  }
  .services-map {
    iframe {
      height: 640px;
    }
  }
  
  /* shop-page */
  
  .shop-title {
    font-size: 18px;
    height: 80px;
  }
  
}
